const moveDateToTag = () => {
  document.querySelectorAll(".blog-index__post").forEach(post => {
    debugger;
    const placeToPutTheDate = post.querySelector(".blog-index__post-content > div");
    if (!placeToPutTheDate) {
      return;
    }

    const date = post.querySelector(".blog-index__post-date");
    if (!date) {
      return;
    }

    const newDate = date.cloneNode(true);
    newDate.classList.add("digiphy-custom");

    placeToPutTheDate.prepend(newDate);

    date.classList.add("hidden");
  });
};

